<template lang="html">
  <div class="">
    <div style="z-index: 9;" class="techsupport" id="techsupport" :class="{'full-width': fullWidth, 'absolute': absolute}" @click="openTech()" v-clickoutside="closeTech">
      <div class="i i-techsupport">

      </div>
      <div class="info">
        <div class="text-color-white-trans text-bold text-size-m">
          Техподдержка
        </div>
        <div class="text-color-white text-bold text-size-ml phone">
          <a href="tel:+79296198199">+7(929)619-81-99</a>
        </div>
        <div class="text-color-white-trans text-size-s">
          По будням с 9:00 до 18:00 (МСК)
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Techsupport',
  data: function(){
    return {
      fullWidth: false,
      absolute: false,
    }
  },
  watch: {
    '$route'(after,before){
      this.checkOffset();
    }
  },
  mounted(){
    let self = this;
    this.checkOffset();
    document.addEventListener("scroll", function(){
      self.checkOffset();
    });
  },
  methods: {
    openTech(){
        this.fullWidth = true;
    },
    closeTech(){
        this.fullWidth = false;
    },
    checkOffset() {
      function getRectTop(el){
        var rect = el.getBoundingClientRect();
        return rect.top;
      }
      let socialFloat = document.querySelector('#techsupport');
      let footer = document.querySelector('.footer');
      if((getRectTop(socialFloat) + document.body.scrollTop) + socialFloat.offsetHeight >= (getRectTop(footer) + document.body.scrollTop) - 24)
        if(!document.body.classList.contains('no-footer')){
          this.absolute = true;//socialFloat.classList.add('absolute');
        }
      if(document.body.scrollTop + window.innerHeight < (getRectTop(footer) + document.body.scrollTop))
        this.absolute = false;//socialFloat.classList.remove('absolute');
    }
  },
  directives: {
      'clickoutside': {
          bind: function(el, binding, vNode) {
              if (typeof binding.value !== 'function') {
                  const compName = vNode.context.name;
                  let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
                  if (compName) { warn += `Found in component '${compName}'` }
                  //console.warn(warn);
              }
              const bubble = binding.modifiers.bubble;
              const handler = (e) => {
                  if (bubble || (!el.contains(e.target) && el !== e.target) && (!e.target.classList.contains('click-outside-tooltip'))) {
                      binding.value(e)
                  }
              }
              el.__vueClickOutside__ = handler;
              document.addEventListener('click', handler);
          },

          unbind: function(el, binding) {
              document.removeEventListener('click', el.__vueClickOutside__);
              el.__vueClickOutside__ = null;
          }
      }
  }
}
</script>

<style lang="css" scoped>
</style>
