import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/partner',
            name: 'partner',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: function () {
                return import(/* webpackChunkName: "about" */ '../views/Partner.vue')
            },
        },
        {
            path: '/partner/coupon-edit/id:id',
            name: 'action-edit',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: function () {
                return import(/* webpackChunkName: "actions" */ '../views/partner/ActionEdit.vue')
            },
        },
        {
            path: '/partner/coupons/:status/id:id',
            name: 'action',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: function () {
                return import(/* webpackChunkName: "actions" */ '../views/partner/Action.vue')
            },
        },
        {
            path: '/partner/coupons/:status',
            name: 'actions',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: function () {
                return import(/* webpackChunkName: "actions" */ '../views/partner/Actions.vue')
            },
        },
        {
            path: '/partner/coupons',
            name: 'actions-default',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: function () {
                return import(/* webpackChunkName: "actions" */ '../views/partner/Actions.vue')
            },
        },
        {
            path: '/partner/team',
            name: 'team',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: function () {
                return import(/* webpackChunkName: "team" */ '../views/partner/Team.vue')
            },
        },
        {
          // партнер - бухгалтерия - Новые купоны (колонка макетов 29)
          path: '/partner/commission',
          name: 'buh-new',
          component: function () {
            return import(/* webpackChunkName: "buhpartner" */ '../views/partner/BuhNew.vue')
          }
        },
        {
          // партнер - бухгалтерия - Счета (колонка макетов 29)
          path: '/partner/commission/bill',
          name: 'buh-invoices',
          component: function () {
            return import(/* webpackChunkName: "buhpartner" */ '../views/partner/BuhInvoices.vue')
          }
        },
        {
          // партнер - бухгалтерия - Счета (колонка макетов 29)
          path: '/partner/commission/statistic',
          name: 'partnerStatistic',
          component: function () {
            return import(/* webpackChunkName: "statistic" */ '../views/partner/Statistic.vue')
          }
        },
        {
          // партнер - О поставщике - Редактор (колонка макетов 21)
          path: '/partner/about-edit',
          name: 'partnerAbout',
          component: function () {
            return import(/* webpackChunkName: "about" */ '../views/partner/PartnerAbout.vue')
          }
        },
        {
          // партнер - О поставщике - Превью (колонка макетов 21)
          path: '/partner/about',
          name: 'partnerAboutPreview',
          component: function () {
            return import(/* webpackChunkName: "about" */ '../views/partner/PartnerAboutPreview.vue')
          }
        },
        {
          // партнер - Превью заполненной акции (колонка макетов 23)
          path: '/partner/action-preview',
          name: 'partnerPreviewAction',
          component: function () {
            return import(/* webpackChunkName: "about" */ '../views/partner/PartnerPreviewAction.vue')
          }
        },
        {
            // партнер - отзывы
            path: '/partner/opinion',
            name: 'partnerOpinion',
            component: function () {
                return import(/* webpackChunkName: "buhpartner" */ '../views/partner/Opinion.vue')
            }
        },
        {// конструктор "о поставщике" - макеты 21
            path: '/partner/partner-contact-page',
            name: 'partnerContractPage',
            component: () => import(/* webpackChunkName: "payment" */ '../views/partner/PartnerContractPage.vue')
        },
        {// погашение купонов - макеты 26
            path: '/partner/repayment',
            name: 'Repayment',
            component: () => import(/* webpackChunkName: "repayment" */ '../views/partner/Repayment.vue')
        },
        {// партнер - правила (31)
            path: '/partner/rules',
            name: 'partnerRules',
            component: () => import(/* webpackChunkName: "rules" */ '../views/partner/PartnerRules.vue')
        },
        {// партнер - купоны в работе
            path: '/partner/coupons-work',
            name: 'partnerCouponsWork',
            component: () => import(/* webpackChunkName: "statistic" */ '../views/partner/CouponsWork.vue')
        },
        {// партнер - купоны в работе
            path: '/partner/new-coupons',
            name: 'partnerNewCoupons',
            component: () => import(/* webpackChunkName: "statistic" */ '../views/partner/NewCoupons.vue')
        },
    ]
})
