<template>
    <div class="page-warning" v-if="cookiesWarning">
        <div class="inner-wrapper">
            <div class="page-warning__text">
			<span class="icon">
				<span class="i i-info"></span>
			</span>

                <span class="t">Используя веб-сайт ZEMSKIDKI.RU, вы соглашаетесь на использование файлов <strong>cookies</strong>.</span>
            </div>


            <div class="page-warning__close" @click="hideCookiesWarning">
                <span class="i i-close"></span>
                <span class="t">Больше не показывать</span>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'

    export default {
        name: "CookiesWarning",
        computed: {
            ...mapState({
                cookiesWarning: state => state.users.cookiesWarning,
            })
        },
        methods: {
            hideCookiesWarning() {
                this.$store.commit('users/hideCookiesWarning');
            },
        }

    }
</script>

