<template>
    <div class="header-category">
        <div class="inner-wrapper">
            <div class="header-category__menu">
                <div class="item" v-for="item in menu" :key="item.id">
                    <router-link :to="'/partner/coupons/'+item.code" :class="{active:item.code===currentStatus}">{{item.name}}<sup v-if="item.id !== '21' && item.elements > 0">{{item.elements}}</sup></router-link>
                </div>
            </div>
            <!-- end header-category__menu -->


            <div class="header-category__right" v-if="permissionEdit">
                <router-link class="header-category__add-action" to="/partner/coupon-edit/idnew">
                    <span class="plus">+</span> Создать акцию
                </router-link>
            </div>
        </div>
        <!-- end inner-wrapper -->
    </div>
    <!-- end header-category -->
</template>

<script>
    import {mapState} from 'vuex';

    export default {
        name: "ActionMenu",
        created() {
            this.$store.dispatch('partnerActions/getMenu');
        },
        computed: {
            ...mapState({
                menu:  state => state.partnerActions.menu,
                user: state => state.users.user,
                defaultStatus:  state => state.partnerActions.defaultStatus,
                defaultStatusCode:  state => state.partnerActions.defaultStatusCode,
            }),
            currentStatus() {
                return this.$route.params.status ? this.$route.params.status : this.defaultStatusCode
            },
            permissionEdit(){
                if(this.user.permission && this.user.permission.indexOf(6) === -1){
                    return false;
                }
                return true;
            }
        },
    }
</script>

