<template>
    <div class="all-wrapper grey-wrapper" id="app">
        <div v-if="!isLoadedPartner" class="preloader-page">
            <div class="preloader"></div>
        </div>
        <div class="warning-main --red --fixed" v-if="showNotificationPartnerVerify">
            <div class="inner-wrapper">
                <div class="l --icon">
                    <span class="icon">
                        <span class="i i-warning"></span>
                    </span>

                    <div class="t">Покупатели увидят ваши акции только после того, как мы получим оригинал договора с вашей печатью и подписью...</div>
                </div>

                <div class="r"></div>
            </div>
            <!-- end inner-wrapper -->
        </div>
        <!-- end warning-main -->


        <header :class="{'header-moderator': !( isAboutEditPage || isActionPreviewPage || isActionEditPage), '--add': isActionPage || (isAuditorPage && user.permission.indexOf(9) !== -1), 'header--black': isAboutEditPage || isActionPreviewPage || isActionEditPage}">
            <div class="header-main" :class="{'header-main--editor': isAboutEditPage || isActionPreviewPage || isActionEditPage}">
                <div class="inner-wrapper">
                    <div class="h-left">
                        <div class="h-editor-text"
                             v-if="isActionEditPage && user.permission.indexOf(6) !== -1">
                            <span class="i i-info" style="opacity: 0.7"></span>
                            <span class="t text-size-xs" style="opacity: 0.7">Перед отправкой акции на модерацию обязательно оцените её внешний вид. Для этого заполните все поля и сохраните её.</span>
                        </div>
                        <div class="h-logo">
                            <router-link class="h-logo-link" to="/partner">
                                <span class="icon"></span>
                                <span class="t">partner</span>
                            </router-link>

                            <tooltip ref="tooltipNotifyReset"
                             :direction="'bottom-center-direction'"
                             :add-class-wrap="'h-logo__tooltip'"
                             :isBtnClose="false"
                             :add-style="{width: 175 + 'px'}">
                                <span slot="tooltipBtn"></span>

                                <div slot="tooltipContent">
                                    <div class="text-center">
                                        <div>
                                            <a href="" class="i i-refresh2 i--blue"></a>
                                        </div>
                                        <div class="text-size-s" style="margin-top: 4px; ">
                                            Мы обновили сайт.<br>
                                            Чтобы все заработало –<br>
                                            <a href="">перезагрузите</a> страницу
                                        </div>
                                    </div>
                                </div>
                            </tooltip>
                        </div>

                        <search-repayment v-if="isShowMenu"></search-repayment>
                        <div class="h-nav-wrap" v-if="isShowMenu">

                            <top-menu :type="'partner'"></top-menu>
                        </div>

                         <div class="h-editor-back" v-if="isActionPreviewPage">
                            <router-link :to="'/partner/coupons/'+codeByStatusId(action.status)" class="h-editor-link">
                                <span class="i i-back"></span>
                                <span class="t" v-if="+action.status === statuses.develop">Акции в разработке</span>
                                <span class="t" v-if="+action.status === statuses.active">Опубликованные акции</span>
                                <span class="t" v-if="+action.status === statuses.archive">Приостановленные акции</span>
                                <span class="t" v-if="+action.status === statuses.deleted">Удалённые акции</span>
                                <span class="t" v-if="+action.status === statuses.ban">Заблокированные акции</span>
                            </router-link>
                        </div>
                    </div>
                    <!-- end h-left -->




                    <div class="h-right">
                        <div class="h-right__editor" v-if="(isAboutEditPage  && user.permission.indexOf(5) !== -1) || (isActionPreviewPage  && user.permission.indexOf(6) !== -1) || (isActionEditPage  && user.permission.indexOf(6) !== -1)">
                            <router-link v-if="isAboutEditPage" to="/partner/about/" class="h-editor-link">Отмена</router-link>


                            <div class="text" v-if="isActionPreviewPage && +action.status === statuses.deleted">
                                <span class="i i-info mr4 mt-2"></span>
                                <span class="t">
                                    <span class="t__note">С удалённой акцией нельзя ничего делать. Но вы можете создать копию и делать с ней, что хотите...</span>
                                </span>
                            </div>
                            <div class="text" v-if="isActionPreviewPage && +action.status === statuses.ban">
                                <span class="i i-warning i--orange mr8 mt-1"></span>

                                <span class="t">
                                    <span class="t__note text-color-orange text-size-s">Эта акция заблокирована модератором {{action.statusDate}} Подробности на {{action.provider.email}}</span>
                                </span>
                            </div>

                            <template v-if="+action.status === statuses.active  && isActionPreviewPage">
                                <div class="text" v-if="action.lk_status_publicate == 'NEW'">
                                    <span class="i i-gear i--yellow mr8 mt-1"></span>
                                    <span class="t">
                                        <span class="t__note text-color-yellow text-size-s">Акция находится на срочной проверке<br> и будет опубликована в ближайшее время</span>
                                    </span>
                                </div>

                                <div class="text" v-if="action.lk_status_publicate == 'CHECK'">
                                    <span class="i i-gear i--yellow mr8 mt-1"></span>

                                    <span class="t">
                                        <span class="t__note text-color-yellow text-size-s">Текущая редакция находится на проверке.<br> Покупатели пока видят старую версию от {{action.publicate_date}}</span>
                                    </span>
                                </div>

                            </template>

                            <!-- <div class="text">
                                <span class="i i-info mr4 mt-2"></span>

                                <span class="t">
                                    <span class="t__note">С удалённой акцией нельзя ничего делать. Но вы можете создать копию и делать с ней, что хотите...</span>
                                </span>
                            </div> -->

                            <!-- <div class="text">
                                <span class="i i-warning i--orange mr8 mt-1"></span>

                                <span class="t">
                                    <span class="t__note text-color-orange text-size-s">Эта акция заблокирована модератором 12.08.2019 Подробности на nelfeelingood@gmail.com</span>
                                </span>
                            </div> -->

                            <!-- <div class="text">
                                <span class="i i-gear i--yellow mr8 mt-1"></span>

                                <span class="t">
                                    <span class="t__note text-color-yellow text-size-s">Текущая редакция находится на проверке.<br> Покупатели пока видят старую версию от 14.02.2019</span>
                                </span>
                            </div> -->

                            <!-- <div class="text">
                                <span class="i i-gear i--yellow mr8 mt-1"></span>

                                <span class="t">
                                    <span class="t__note text-color-yellow text-size-s">Акция находится на срочной проверке<br> и будет опубликована в ближайшее время</span>
                                </span>
                            </div> -->

                            <span v-if="isActionPreviewPage && +action.status === statuses.develop"
                                  @click="deleteAction"
                                  class="btn h-editor-btn btn--ico">
                                <span class="i i-trash"></span>
                                <span class="t">Удалить</span>
                            </span>

                            <span v-if="isActionPreviewPage  && +action.status === statuses.archive"
                                  @click="activeAction"
                                  class="btn h-editor-btn btn--ico">
                                <span class="i i-send"></span>
                                <span class="t">Опубликовать повторно</span>
                            </span>

                            <submit-btn tag="span"
                                        v-if="isActionPreviewPage  && +action.status === statuses.active && action.lk_status_publicate == 'NEW'"
                                        class="btn h-editor-btn btn--ico"
                                        size="small"
                                        @click.native="cancelAction"
                                        :type="buttons.cancel.state">
                                <span class="i i-close2" v-if="buttons.cancel.state === 'normal'"></span>
                                <span class="t">Отменить публикацию</span>
                            </submit-btn>

                            <submit-btn tag="span"
                                        v-if="isActionPreviewPage  && +action.status === statuses.active && action.lk_status_publicate != 'NEW'"
                                        class="btn h-editor-btn btn--ico"
                                        size="small"
                                        @click.native="archiveAction"
                                        :type="buttons.stop.state">
                                <span class="i i-pause3" v-if="buttons.stop.state === 'normal'"></span>
                                <span class="t">Приостановить</span>
                            </submit-btn>

                            <submit-btn tag="span" v-if="isActionPreviewPage  && +action.status === statuses.develop"
                                        class="btn h-editor-btn btn--ico"
                                        size="small"
                                        @click.native="copyAction"
                                        :type="buttons.copy.state">
                                <span class="i i-copy" v-if="buttons.copy.state === 'normal'"></span>
                                <span class="t">Создать копию</span>
                            </submit-btn>

                            <router-link tag="span" :to="'/partner/coupon-edit/id'+action.id" v-if="isActionPreviewPage && (+action.status === statuses.develop || +action.status === statuses.archive || +action.status === statuses.active)" class="btn h-editor-btn btn--ico">
                                <span class="i i-pencil"></span>
                                <span class="t">Редактировать</span>
                            </router-link>

                            <submit-btn tag="span" v-if="isActionPreviewPage  && +action.status !== statuses.develop"
                                        class="btn h-editor-btn btn--ico"
                                        size="small"
                                        @click.native="copyAction"
                                        :type="buttons.copy.state">
                                <span class="i i-copy" v-if="buttons.copy.state === 'normal'"></span>
                                <span class="t">Создать копию</span>
                            </submit-btn>

                            <submit-btn tag="span" v-if="isActionPreviewPage && +action.status === statuses.develop"
                                        class="btn h-editor-btn btn--save btn--ico"
                                        size="small"
                                        @click.native="moderateAction"
                                        :type="buttons.moderateAction.state">
                                <span class="i i-send" v-if="buttons.moderateAction.state === 'normal'"></span>
                                <span class="t">Опубликовать</span>
                            </submit-btn>

                            <submit-btn tag="span" v-if="isAboutEditPage"
                                        class="btn h-editor-btn btn--ico btn--green"
                                        @click.native="saveAboutForm"
                                        size="small"
                                        :type="buttons.savePartner.state">
                                <span class="i i-ok2" v-if="buttons.savePartner.state === 'normal'"></span>
                                <span class="t">Сохранить</span>
                            </submit-btn>

                            <router-link v-if="isActionEditPage"
                                 :to="'/partner/coupons/'+codeByStatusId(this.action.status)+'/'+($route.params.id !== 'new'?'id'+$route.params.id:'')"
                                  class="h-editor-link">
                                Отмена
                            </router-link>

                            <submit-btn tag="span" v-if="isActionEditPage"
                                        class="btn size-s h-editor-btn"
                                        :class="{'btn--ico': currentStep  === 'finish', 'btn--green': currentStep  === 'finish', 'btn--blue': currentStep  !== 'finish'}"
                                        @click.native="saveAction"
                                        size="small"
                                        :type="buttons.saveAction.state">
                                <span class="i i-ok2" v-if="buttons.saveAction.state === 'normal' && currentStep  === 'finish'"></span>
                                <span class="t" v-if="currentStep  === 'finish'">Сохранить</span>
                                <span class="t" v-else>Продолжить</span>
                            </submit-btn>
                        </div>

                        <router-link v-if="isShowMenu" to="/partner/rules/" class="h-link dif h-link--icon"><span class="i i-info"></span></router-link>

                        <div v-if="isShowMenu || partner.isNew">
                            <tooltip v-if="user.firstName"
                                     :direction="'bottom-right-direction'"
                                     :add-class-wrap="'h-user'"
                                     :add-class-btn="'l'"
                                     :add-class="'h-user-menu'"
                                     :is-padding="false">
                                <span slot="tooltipBtn">
                                    <span class="t">
                                        {{ user.firstName ? user.firstName[0] : '' }} <!-- {{ user.lastname[0] }}. -->
                                    </span>
                                </span>

                                <div slot="tooltipContent">
                                    <div class="h-user-menu__wrap">
                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-man2"></span>
                                            </span>
                                            <span class="t">{{ user.lastName }} {{ user.firstName }} {{ user.secondName }}</span>
                                        </div>

                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-phone"></span>
                                            </span>
                                            <span class="t">{{ user.phone }}</span>
                                        </div>

                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-mail"></span>
                                            </span>
                                            <span class="t">{{ user.email }}</span>
                                        </div>

                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-marker"></span>
                                            </span>
                                            <span class="t">{{ partner.cityName }}</span>
                                        </div>

                                    </div>

                                    <div class="h-user-menu__bottom">
                                        <a href="javascript:void(0);" class="link"  @click="logout()">
                                            <span class="i i-exit2"></span>
                                            <span class="t">ВЫЙТИ</span>
                                        </a>
                                    </div>
                                </div>
                            </tooltip>
                        </div>
                    </div>
                </div>
                <!-- end inner-wrapper -->
            </div>
            <!-- end header-main -->

            <action-menu v-if="isActionPage"></action-menu>
            <div v-if="isAuditorPage && user.permission.indexOf(9) !== -1" class="header-category">
                <div class="inner-wrapper">
                    <div class="header-category__menu">
                        <div class="item">
                            <router-link exact active-class="active" to="/partner/commission">Погашенные купоны</router-link>
                        </div>
                        <div class="item">
                            <router-link exact active-class="active" to="/partner/commission/bill">Выставленные счета
                                <template v-if="countersPartner && countersPartner.buh && this.$route.name!='buh-invoices'">
                                    <span v-if="countersPartner.buh.bill" class="num" :class="{'--orange':countersPartner.buh.bill=='orange'}">!</span>
                                </template>
                            </router-link>
                        </div>
                        <div class="item">
                            <router-link exact active-class="active" to="/partner/commission/statistic">Ваша статистика</router-link>
                        </div>
                    </div>
                    <!-- end header-category__menu -->
                </div>
                <!-- end inner-wrapper -->
            </div>
        </header>

        <main v-show="isLoadedPartner" id="main">
            <router-view v-if="partner.success"/>
            <section v-else>
                <div class="content">
                    <div class="main-title">
                        <div class="main-title__wrap main-title--nowrap">
                            <div class="h1">Личный кабинет Партнёра</div>
                        </div>
                        <div class="main-title__right"></div>
                    </div>
                    <section class="container">
                        <auth :type-lk="'partner'" v-if="!token && !isBanned && !isBannedPartner"></auth>
                        <div class="wrapper-content" v-else-if="isBanned && !isBannedPartner">
                            <div class="sect-blank">
                                <div class="wrap text-center">
                                    <div class="text-size-l text-bolder text-color-red mt16">Слишком много попыток входа!</div>
                                    <div class="mt">Вы заблокированы на 12 часов.</div>
                                    <div class="mt">Если у вас что-то срочное, <a href="javascript:void(0);" @click="openMailZems">напишите</a> руководителю проекта</div>
                                </div>

                                <div class="mini-zems"></div>
                            </div>
                            <!-- end sect-blank -->
                        </div>
                        <div class="wrapper-content" v-else>
                            <div class="sect-blank">
                                <div class="wrap text-center" v-if="partner.isBan">
                                    <div class="text-size-l text-bolder text-color-red mt16">Личный кабинет #{{ partner.login }} заблокирован!</div>
                                    <div class="mt">Причина блокировки: {{ partner.banReason }}</div>
                                    <div class="mt">Если это произошло по ошибке, <a href="javascript:void(0);" @click="openMailZems">напишите</a> руководителю проекта</div>
                                </div>
                                <div class="wrap text-center" v-else-if="partner.isRefuse">
                                    <div class="text-size-l text-bolder text-color-red mt16">Действие кабинета #{{ partner.login }} приостановлено!</div>

                                    <div class="mt">Если это произошло по ошибке, <a href="javascript:void(0);" @click="openMailZems">напишите</a> руководителю проекта</div>
                                </div>
                                <div class="wrap text-center" v-else>
                                    <div class="text-size-l text-bolder text-color-red mt16">
                                        У номера {{(user.phone)?user.phone:partner.login}}<br>
                                        нет доступа в этот раздел
                                    </div>
                                </div>
                            </div>
                            <!-- end sect-blank -->
                        </div>
                    </section>
                </div>
            </section>
        </main>

        <cookies-warning v-show="isLoadedPartner"></cookies-warning>
        <tech-support ></tech-support>
        <vue-footer :hide-offer="true"></vue-footer>
        <popup :show="popups.citySelect.show"
               :width="456"
               :add-class="'popup-city'"
               :name="popups.citySelect.name">
            <city-select :is-show-all="false"></city-select>
        </popup>
        <popup :is-set-styled="true" :show="popups.mailZems.show" :name="popups.mailZems.name">
            <mail-zems></mail-zems>
        </popup>
    </div>
</template>




<script>
    import { mapState } from 'vuex'
    import TopMenu from '@/components/TopMenu.vue'
    import ActionMenu from '@/components/partner/actions/ActionMenu.vue'
    import SearchTop from '@/components/SearchTop.vue'
    import Popup from '@/components/modal/Popup.vue'
    import CitySelect from '@/components/CitySelect.vue'
    import MailZems from '@/components/modal/MailZems.vue'
    import Auth from '@/components/user/auth/AuthPersonal.vue'
    import ManyAuth from '@/components/modal/ManyAuth.vue'
    import Tooltip from '@/components/Tooltip.vue';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'
    import VueFooter from '@/components/VueFooter.vue'
    import CookiesWarning from '@/components/CookiesWarning.vue'
    import {scrollTo} from '@/helpers/animate.js';
    import SearchRepayment from '@/components/partner/SearchRepayment.vue'
    import TechSupport from '@/components/partner/Techsupport.vue'

    export default {
        components: {
            TopMenu,
            ActionMenu,
            SearchTop,
            Popup,
            CitySelect,
            Auth,
            MailZems,
            ManyAuth,
            Tooltip,
            SubmitBtn,
            VueFooter,
            CookiesWarning,
            SearchRepayment,
            TechSupport
        },
        created() {
            this.$store.dispatch('users/getCurrent');
            this.$store.dispatch('partner/getCurrent').then(()=>{
                if(this.partner.redirect && this.$route.name !== 'partner') {
                    this.$router.push(this.partner.redirect);
                }
            });
            this.$store.dispatch('cities/setCityCookies');
            this.$store.dispatch('actions/setFavoritesCookies');
            this.$store.dispatch('actions/setVisitedCookies');
            this.$store.dispatch('users/getCookiesWarning');
            this.$store.dispatch('cities/getCities');
            this.$store.dispatch('options/getList');
        },
        mounted(){
            this.$store.subscribe((mutation, state) => {
                if(mutation.type=='popups/open'){
                    if(state.popups.items.notifyPageReset.show==true){
                        this.showNotifyPageReset();
                    }
                }
            });

            let scrollWidth = window.innerWidth - document.documentElement.clientWidth;
            if (scrollWidth == '0') {
                document.getElementsByTagName('body')[0].classList.add("scroll-mac-os");
            }
        },
        data(){
            return {
                buttons: {
                    copy: {
                        state: 'normal'
                    },
                    stop: {
                        state: 'normal'
                    },
                    active: {
                        state: 'normal'
                    },
                    delete: {
                        state: 'normal'
                    },
                    saveAction: {
                        state: 'normal'
                    },
                    savePartner: {
                        state: 'normal'
                    },
                    moderateAction: {
                        state: 'normal'
                    },
                    cancel: {
                        state: 'normal'
                    }
                }
            }
        },
        watch: {
          '$route'(to,from){
            let self = this;
            setTimeout(function(){
              self.checkOffset();
            },10);
          },
          isLoad(after,before){
            if(after == false){
              let self = this;
              setTimeout(function(){
                self.checkOffset();
              },10);
            }
          },
          isLoadedPartner(after,before){
            if(after == true){
              let self = this;
              setTimeout(function(){
                self.checkOffset();
              },10);
            }
          },
        },
        computed: {
          absolute: {
            get(){
              return this.$store.getters[ 'partnerActions/getTechsupportAbsolute' ]
            },
            set( value ){
              this.$store.commit('partnerActions/setTechsupportAbsolute', value);
            },
          },
            isAuditorPage() {
                return this.partner.success && (this.$route.name === 'buh-new' || this.$route.name === 'buh-invoices' || this.$route.name === 'partnerStatistic')
            },
            isActionPage() {
                return this.partner.success && (this.$route.name === 'actions-default' || this.$route.name === 'actions')
            },
            isActionEditPage() {
                return this.partner.success && this.$route.name === 'action-edit'
            },
            isActionPreviewPage() {
                return this.partner.success && this.$route.name === 'action' && this.action !== undefined && this.action.id > 0
            },
            isAboutEditPage() {
                return this.partner.success && this.$route.name === 'partnerAbout'
            },
            isShowMenu() {
                return !(this.$route.name === 'partnerAbout' || this.$route.name === 'action' || this.$route.name === 'action-edit') && this.partner.success  && !this.partner.isNew
            },
            showNotificationPartnerVerify() {
                return this.partner.showNotificationPartnerVerify && !this.partner.isNew && this.partner.success;
            },
            userCheck() {
                if(!!this.user.firstName && !!this.user.secondName && !!this.user.lastName && !!this.user.email){
                    return true;
                }
                return false;
            },
            token(){
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
            ...mapState({
                popups: state => state.popups.items,
                partner: state => state.partner.current,
                action: state => state.partnerActions.detail,
                statuses: state => state.partnerActions.statuses,
                user: state => state.users.user,
                isLoadedPartner: state => state.partner.isLoaded,
                isBanned: state => state.auth.isBanned,
                isBannedPartner: state => state.auth.isBannedPartner,
                currentCity:  state => state.cities.current,
                currentCityIp:  state => state.cities.currentCityIp,
                currentStep: state => state.partnerActions.currentStep,
                tmpId: state => state.partnerActions.tmpId,
                countersPartner: state => state.partnerCounters.items,
                isLoad: state => state.partnerActions.isLoad,
            }),
        },
        methods: {
            getRectTop(el){
              let rect = el.getBoundingClientRect();
              return rect.top;
            },
            checkOffset() {
              let socialFloat = document.querySelector('#techsupport');
              let footer = document.querySelector('.footer');
              if((this.getRectTop(socialFloat) + document.body.scrollTop) + socialFloat.offsetHeight >= (this.getRectTop(footer) + document.body.scrollTop) - 24)
                this.absolute = true;
              if(document.body.scrollTop + window.innerHeight < (this.getRectTop(footer) + document.body.scrollTop))
                this.absolute = false;
            },
            showPopup(popup) {
                this.$store.dispatch('popups/open',{name:popup});
            },
            openAuthorization: function () {
                this.$store.commit('auth/setIsRedirect', true);
                this.$store.dispatch('popups/open', {name: this.popups.publicAuth.name});
            },
            logout() {
                this.$store.dispatch('auth/logout');
            },
            saveAboutForm() {
                this.buttons.savePartner.state = 'load';
                this.$store.dispatch('partner/saveAboutForm',{}).then((data)=>{
                    this.buttons.savePartner.state = 'ok';
                    setTimeout(()=>{
                        this.buttons.savePartner.state = 'normal';
                        this.$router.push('/partner/about');
                        if(data.isShowModalAboutPage){
                            this.$store.dispatch('popups/open', {name: this.popups.constructPartnerFinal.name});
                        }
                    }, 300);

                }).catch((e)=>{
                    this.buttons.savePartner.state = 'normal';
                    if(!!e.scrollTo){
                        if(e.scrollTo == 'otherTitle'){
                            scrollTo('#scroll-other');
                        } else {
                            scrollTo('#scroll-'+e.scrollTo);
                        }

                    }
                });
            },
            openCitySelect() {
                this.$store.dispatch('popups/open', {name: this.popups.citySelect.name});
            },
            openMailZems: function () {
                this.$store.dispatch('popups/open', {name: this.popups.mailZems.name});
            },
            deleteAction() {
                this.$store.dispatch('popups/open', {name: this.popups.partnerCouponsRemove.name});
            },
            activeAction() {
                this.$store.dispatch('partnerActions/setStatus',{id:this.action.id,status:this.statuses.active}).then(() => {
                    this.$router.push('/partner/coupons/status/active');
                });
            },
            cancelAction() {
                this.$store.dispatch('popups/open', {name: this.popups.partnerCouponsCancel.name});
                /*this.$store.dispatch('partnerActions/setStatus',{id:this.action.id,status:this.statuses.archive}).then(() => {
                    this.$router.push('/partner/coupons/status'+this.statuses.archive);
                });*/
            },
            archiveAction() {
                this.$store.dispatch('popups/open', {name: this.popups.partnerCouponsStop.name});
                /*this.$store.dispatch('partnerActions/setStatus',{id:this.action.id,status:this.statuses.archive}).then(() => {
                    this.$router.push('/partner/coupons/status'+this.statuses.archive);
                });*/
            },
            copyAction() {
                this.buttons.copy.state = 'load';
                this.$store.dispatch('partnerActions/copyAction',{id:this.action.id}).then(() => {
                    this.buttons.copy.state = 'ok';
                    setTimeout(()=>{
                        this.$router.push('/partner/coupons/develop');
                        this.buttons.copy.state = 'normal';
                    }, 300);
                });
            },
            codeByStatusId(id) {
                let code = null,
                    statuses = this.statuses,
                    codes = Object.getOwnPropertyNames(statuses);

                if (id) {
                    for(let c in statuses) {
                        if (id == statuses[c]) {
                            code = c;
                        }
                    }
                } else {
                    code = codes[0];
                }

                return code;
            },
            saveAction() {
                if(this.currentStep !== 'finish'){
                    this.$store.dispatch('partnerActions/nextStep')
                } else {
                    this.buttons.saveAction.state = 'load';
                    let id;
                    if(this.$route.params.id === 'new'){
                        id = this.tmpId;
                    } else {
                        id = this.$route.params.id;
                    }
                    this.$store.dispatch('partnerActions/saveAction',{id:this.$route.params.id}).then(()=> {
                        this.buttons.saveAction.state = 'ok';
                        let id = this.tmpId ? this.tmpId : this.$route.params.id;
                        setTimeout(()=>{
                            if(+id > 0) {
                                this.$router.push('/partner/coupons/'+this.codeByStatusId(this.action.status)+'/id'+id);

                                if(this.action.lk_status_publicate == 'PUBLICK'){
                                    this.$store.dispatch('popups/open', {name: this.popups.actionToModaretionConfirmRepeat.name});
                                }
                            } else {
                                this.$router.push('/partner/coupons/');
                            }
                            this.buttons.saveAction.state = 'normal';
                        }, 300);
                    }, ()=> {
                        this.buttons.saveAction.state = 'normal';
                    })
                }
            },
            moderateAction() {
                if(this.action.isValid){
                    /*this.buttons.moderateAction.state = 'load';
                    this.$store.dispatch('partnerActions/setStatus',{id:this.action.id,status:this.statuses.moderation}).then(() => {
                        this.buttons.moderateAction.state = 'ok';
                        setTimeout(()=>{
                            this.buttons.moderateAction.state = 'normal';
                            this.$router.push('/partner/coupons/moderation');
                        }, 300);
                    });*/
                    this.$store.dispatch('popups/open', {name: this.popups.partnerModerateConfirm.name});
                } else {
                    switch (this.action.validError){
                        case 'field':
                            this.$store.dispatch('popups/open', {name: this.popups.partnerModerateErrorField.name});
                            break;
                        case 'percent':
                            this.$store.dispatch('popups/open', {name: this.popups.partnerModerateErrorPercent.name});
                            break;
                        case 'sum':
                            this.$store.dispatch('popups/open', {name: this.popups.partnerModerateErrorSum.name});
                            break;
                        case 'provider':
                            this.$store.dispatch('popups/open', {name: this.popups.partnerModerateErrorProvider.name});
                            break;
                    }
                }
            },
            showNotifyPageReset(){
                if(this.$refs.tooltipNotifyReset){
                    this.$refs.tooltipNotifyReset.tooltipShow();
                }
            }
        }

    }
</script>



